
.nav-list{position:fixed;top:0;bottom:0;left:-200px;width:200px;background-color:#fff;color:#313131;transition:all .3s ease;z-index:99
}
.nav-list.show{transform:translateX(200px)
}
.list-ul{margin:0 24px;border-top:1px solid #d4d4d4;overflow:hidden;padding-top:9%
}
.list-ul .item{display:block;font-size:14px;font-weight:200;padding:9% 0;text-align:left;text-indent:1px;line-height:15px;color:#313131;font-weight:700
}
.list-ul .item:last-child{margin-bottom:50px
}
.list-ul .item:before{color:#2c3e50
}
.list-ul .line{border-top:1px solid #d4d4d4
}



